import bridge from '@vkontakte/vk-bridge';

export const API = async function (method, params) {
	const allparams = btoa(window.location.search)
		const response = await fetch('https://h154851.srv11.test-hf.su/plague/api.php?key=' + allparams + "&action=" + method + "&" + params)
		const json = await response.json()
		return await json
}

export const GETTOP = async function (SetTopPlayers) {
	  const allparams = btoa(window.location.search)
		const response = await fetch('https://h154851.srv11.test-hf.su/plague/api.php?key=' + allparams + "&action=gettop")
		const json = await response.json()
		SetTopPlayers(json.response)
		return true
}

export const GETFRIENDS = async function (setFriends, apitoken) {
	 const data = await bridge.send("VKWebAppCallAPIMethod", {"method": "friends.get", "params": {"v":"5.131", "access_token": apitoken}});
	 const allparams = btoa(window.location.search)
	 const response = await fetch('https://h154851.srv11.test-hf.su/plague/api.php?key=' + allparams + "&action=getfriends", {
					 method: 'POST',
					 body: data.response.items.join()
	 });
	 const json = await response.json()
	 setFriends(json)
}

export const REGISTER = async function (virusname, owner) {
	const allparams = btoa(window.location.search)
	async function fetchData() {
		const user = await bridge.send('VKWebAppGetUserInfo');
		return await register(user)
	}
	return await fetchData();
	async function register(info){
		const response = await fetch('https://h154851.srv11.test-hf.su/plague/api.php?key=' + allparams + "&action=register&owner=" + owner + "&name=" + encodeURIComponent(virusname), {
						method: 'POST',
						body: JSON.stringify(info)
		});
		const json = await response.json()
		return await json
	}
}
