import React, { useState, useEffect } from 'react';
import anime from 'animejs/lib/anime.es.js';
import bridge from '@vkontakte/vk-bridge';
import { View, ScreenSpinner, Tabbar, TabbarItem,InfoRow,ActionSheet,Snackbar,FormLayout,FormItem ,Radio,Banner,Div,Button,ActionSheetItem, AdaptivityProvider, AppRoot } from '@vkontakte/vkui';
import '@vkontakte/vkui/dist/vkui.css';

import Home from './panels/Home';
import TopUsers from './panels/TopUsers';
import Settings from './panels/Settings';
import Persik from './panels/Persik';
import StartPage from './panels/StartPage';
import CreateVirus from './panels/CreateVirus';
import Loading from './panels/Loading';

import {API, GETFRIENDS} from './panels/api';

import { Icon28AllCategoriesOutline,Icon28PollSquareOutline,Icon28SettingsOutline } from '@vkontakte/icons';

const App = () => {
	const [activePanel, setActivePanel] = useState('loading');
	const [fetchedUser, setUser] = useState(null);
	const [ActiveContent, setActiveContent] = useState('virus');
	const [popout, setPopout] = useState(null);
	const [Info, setInfo] = useState([]);
	const [TopContent, setTopContent] = useState(['check']);
	const [TopPlayers, setTopPlayers] = useState([]);
	const [Referals, setReferals] = useState([]);
	const [Friends, setFriends] = useState(['check']);

	useEffect(() => {
		bridge.subscribe(({ detail: { type, data }}) => {
			if (type === 'VKWebAppUpdateConfig1') {
				const schemeAttribute = document.createAttribute('scheme');
				schemeAttribute.value = data.scheme ? data.scheme : 'client_light';
				document.body.attributes.setNamedItem(schemeAttribute);
			}
		});
		async function fetchData() {
			const user = await bridge.send('VKWebAppGetUserInfo');
			setUser(user);
			setPopout(null);
			const check = await API('checkuser', 'id=' + user.id)
			if(check.status === false){
				setActivePanel('startpage')
			}else{
				const info = await API('getmyinfo')
				const referals = await API('getreferals')
				setInfo(info.response)
				setReferals(referals.response)
				console.log(referals.response)
				setActivePanel('home')
			}
		}
		fetchData();

		function AnimTabs(id){
			anime({
             targets: [document.getElementById("btntop"), document.getElementById("btnhome"), document.getElementById("btnsettings")],
						 scale: 0.8,
						 color: '#7f8285',
						 translateY: 3
           })

		   anime({
             targets: id,
             scale: [1, 1.6],
						 color: '#ad8ef5',
						 translateY: -5
           });
	    }

		var btnhome = document.getElementById("btnhome")
		btnhome.addEventListener('click', () => {AnimTabs(btnhome)});

		var btntop = document.getElementById("btntop")
		btntop.addEventListener('click', () => {AnimTabs(btntop)});

		btntop.addEventListener('click', GetFriends);
		async function GetFriends(){
			if(Friends[0] != 'check') return true
			setFriends(['check'])
			try{
				const data = await bridge.send("VKWebAppGetAuthToken", {"app_id": 7931491, "scope": "friends"})
				if(data.access_token) {
					GETFRIENDS(setFriends, data.access_token)
					btntop.removeEventListener('click', GetFriends);
				}else{
					setFriends(['token'])
				}
			}catch(e){
				setFriends(['token'])
			}
		}

		var btnsettings = document.getElementById("btnsettings")
		btnsettings.addEventListener('click', () => {AnimTabs(btnsettings)});
		AnimTabs(btnhome)

	}, []);

	const go = e => {
		setActivePanel(e.currentTarget.dataset.to);
	};

	const BuyPerk = () => {
		setPopout(
			<ActionSheet onClose={() => setPopout(null)}>
			<Div>
			<InfoRow header="Способность вашего вируса">
       Выбери одну способность для твоего вируса.
      </InfoRow>
			</Div>
			<FormLayout>
          <FormItem top="Зараженный не сможет:">
            <Radio id="radio" name="radio" value="update" defaultChecked={Info.virus.perk == 'update' ? 'defaultChecked' : ''}>Прокачивать свой вирус</Radio>
            <Radio id="radio" name="radio" value="zaraza" defaultChecked={Info.virus.perk == 'zaraza' ? 'defaultChecked' : ''}>Заражать других людей</Radio>
            <Radio id="radio" name="radio" value="buy" defaultChecked={Info.virus.perk == 'buy' ? 'defaultChecked' : ''}>Покупать предметы</Radio>
						<Button style={{marginTop: '8px'}} stretched className="buttons" onClick={async () => {
							var radios = document.getElementsByName('radio')
							for(var i = 0, length = radios.length; i < length; i++){
								if(radios[i].checked === true){
									const response = await API('updatevirus', 'type=perk&value=' + radios[i].value);
									response.status === 'good' ? i = 99 : ''
									response.status === 'good' ? Info.virus.perk = response.response : Info.virus.perk = 'update'
									response.status === 'good' ? setPopout(null) : ''
								}
							}
						}}>Сохранить</Button>
          </FormItem>
        </FormLayout>
			</ActionSheet>
		)
	};

	const BuyVirus = () => {
		setPopout(
			<ActionSheet
			 onClose={() => setPopout(null)}
		 >
			 <Banner
        mode="image"
        header="Заразность"
        subheader="Прокачать заразность на 1%"
        background={
          <div
            style={{
              backgroundColor: '#ad8ef5',
              backgroundImage: 'url(https://i.imgur.com/PxORaDd.png)',
              backgroundPosition: 'right bottom',
              backgroundSize: 100,
              backgroundRepeat: 'no-repeat',
            }}
          />
        }
        actions={<Button mode="overlay_primary" onClick={async () => {
					const response = await API('updatevirus', 'type=zaraza')
					response.status == 'good' ? Info.balance = response.response.balance.toFixed(3) : ''
					response.status == 'good' ? Info.virus.zaraza += 1 : ''
					response.status == 'good' ? Info.virus.buyzaraza += 1 : ''
					response.status == 'good' ? setPopout(<Snackbar duration={1000} onClose={() => setPopout(null)}> Успешно прокачено </Snackbar>) : ''
					response.status == 'error' ? setPopout(<Snackbar duration={1000} onClose={() => setPopout(null)}> {response.message} </Snackbar>) : ''
				}}>Прокачать за {(0.05*(1.35**+Info.virus.buyzaraza)).toFixed(3)} ДНК</Button>}
      />
			<Banner
			 mode="image"
			 header="Цена излечения"
			 subheader="Прокачать цену излечения на 0.1 ДНК"
			 background={
				 <div
					 style={{
						 backgroundColor: '#ad8ef5',
						 backgroundImage: 'url(https://i.imgur.com/d8XC6pE.png)',
						 backgroundPosition: 'right bottom',
						 backgroundSize: 100,
						 backgroundRepeat: 'no-repeat',
					 }}
				 />
			 }
			 actions={<Button mode="overlay_primary" onClick={async () => {
				 const response = await API('updatevirus', 'type=out')
				 response.status == 'good' ? Info.balance = response.response.balance.toFixed(3) : ''
				 response.status == 'good' ? Info.virus.out = response.response.newout.toFixed(3) : ''
				 response.status == 'good' ? Info.virus.buyout += 1 : ''
				 response.status == 'good' ? setPopout(<Snackbar duration={1000} onClose={() => setPopout(null)}> Успешно прокачено </Snackbar>) : ''
				 response.status == 'error' ? setPopout(<Snackbar duration={1000} onClose={() => setPopout(null)}> {response.message} </Snackbar>) : ''
			 }}>Прокачать за {(0.1*(1.35**+Info.virus.buyout)).toFixed(3)} ДНК</Button>}
		 />
			 </ActionSheet>
		)
	};

	const BuyItem = () => {
		setPopout(
			<ActionSheet
			 onClose={() => setPopout(null)}
		 >
		 <Div>
		 <InfoRow header="Купить предметы">
			Некоторые предметы можно применять на друзей и других игроков. С каждым разом цена предмета повышается.
		 </InfoRow>
		 </Div>
			 <Banner
        mode="image"
        header="Маска"
        subheader="Маска отражает атаку вируса с вероятностью 50%. Действует автоматически при следуещей атаке вирусом."
        background={
          <div
            style={{
              backgroundColor: '#ad8ef5',
              backgroundImage: 'url(https://i.imgur.com/IKSK8Un.png)',
              backgroundPosition: 'right bottom',
              backgroundSize: 70,
              backgroundRepeat: 'no-repeat',
            }}
          />
        }
        actions={<Button mode="overlay_primary" onClick={async() => {
					const response = await API('buyitems','item=maska')
				  response.status === 'good' ? Info.items.maska.count = response.response.count : ''
					response.status === 'good' ? Info.items.maska.buy = response.response.newprice : ''
					response.status === 'good' ? Info.balance = response.response.balance.toFixed(3) : ''
					response.status === 'good' ? setPopout(<Snackbar duration={1000} onClose={() => setPopout(null)}> Предмет куплен </Snackbar>) : ''
					response.status === 'error' ? setPopout(<Snackbar duration={1000} onClose={() => setPopout(null)}> {response.message} </Snackbar>) : ''
				}}>Купить за {(0.01*(1.35**+Info.items.maska.buy)).toFixed(3)} ДНК</Button>}
      />
			<Banner
			 mode="image"
			 header="Перчатки"
			 subheader="Перчатки отражают атаку вируса с вероятностью 80%. Действует автоматически при следуещей атаке вирусом."
			 background={
				 <div
					 style={{
						 backgroundColor: '#ad8ef5',
						 backgroundImage: 'url(https://i.imgur.com/HFRg3T3.png)',
						 backgroundPosition: 'right bottom',
						 backgroundSize: 70,
						 backgroundRepeat: 'no-repeat',
					 }}
				 />
			 }
			 actions={<Button mode="overlay_primary" onClick={async() => {
				 const response = await API('buyitems','item=perchatki')
				 response.status === 'good' ? Info.items.perchatki.count = response.response.count : ''
				 response.status === 'good' ? Info.items.perchatki.buy = response.response.newprice : ''
				 response.status === 'good' ? Info.balance = response.response.balance.toFixed(3) : ''
				 response.status === 'good' ? setPopout(<Snackbar duration={1000} onClose={() => setPopout(null)}> Предмет куплен </Snackbar>) : ''
				 response.status === 'error' ? setPopout(<Snackbar duration={1000} onClose={() => setPopout(null)}> {response.message} </Snackbar>) : ''
			 }}>Купить за {(0.01*(1.35**+Info.items.perchatki.buy)).toFixed(3)} ДНК</Button>}
		 />
		 <Banner
			mode="image"
			header="Антисептик"
			subheader="Антисептиком можно вылечить любого игрока, кроме себя."
			background={
				<div
					style={{
						backgroundColor: '#ad8ef5',
						backgroundImage: 'url(https://i.imgur.com/ynNTMOP.png)',
						backgroundPosition: 'right bottom',
						backgroundSize: 70,
						backgroundRepeat: 'no-repeat',
					}}
				/>
			}
			actions={<Button mode="overlay_primary" onClick={async() => {
				const response = await API('buyitems','item=antiseptik')
				response.status === 'good' ? Info.items.antiseptik.count = response.response.count : ''
				response.status === 'good' ? Info.items.antiseptik.buy = response.response.newprice : ''
				response.status === 'good' ? Info.balance = response.response.balance.toFixed(3) : ''
				response.status === 'good' ? setPopout(<Snackbar duration={1000} onClose={() => setPopout(null)}> Предмет куплен </Snackbar>) : ''
				response.status === 'error' ? setPopout(<Snackbar duration={1000} onClose={() => setPopout(null)}> {response.message} </Snackbar>) : ''
			}}>Купить за {(0.01*(1.35**+Info.items.antiseptik.buy)).toFixed(3)} ДНК</Button>}
		/>
			 </ActionSheet>
		)
	};

	return (
		<AdaptivityProvider>
			<AppRoot>
				<View activePanel={activePanel} popout={popout}>
					<Home id='home' setPopout={setPopout} Info={Info} Referals={Referals} BuyVirus={BuyVirus} BuyItem={BuyItem} BuyPerk={BuyPerk} ActiveContent={ActiveContent} setActiveContent={setActiveContent} fetchedUser={fetchedUser} go={go} />
					<TopUsers id='top' Referals={Referals} setReferals={setReferals} Info={Info} Friends={Friends} setFriends={setFriends} setPopout={setPopout} TopPlayers={TopPlayers} setTopPlayers={setTopPlayers} TopContent={TopContent} setTopContent={setTopContent} fetchedUser={fetchedUser} go={go} />
					<Persik id='persik' go={go} />
					<Settings id='settings' go={go} Info={Info} setPopout={setPopout}/>
					<StartPage id='startpage' go={go} />
					<CreateVirus id='createvirus' go={go} setActivePanel={setActivePanel} setInfo={setInfo}/>
					<Loading id='loading' go={go} />
				</View>
				<Tabbar shadow={false} style={{height: '60px',display: activePanel === 'startpage' || activePanel === 'loading' || activePanel === 'createvirus' ? 'none' : '' }}>
				<TabbarItem
					onClick={go}
					selected={activePanel === 'top'}
					data-to="top"
					id="btntop"
				><Icon28PollSquareOutline/></TabbarItem>
				<TabbarItem
              onClick={go}
              selected={activePanel === 'home'}
              data-to="home"
							id="btnhome"
            ><Icon28AllCategoriesOutline/></TabbarItem>
						<TabbarItem
							onClick={go}
							selected={activePanel === 'settings'}
							data-to="settings"
							id="btnsettings"
						><Icon28SettingsOutline/></TabbarItem>
				  </Tabbar>
			</AppRoot>
		</AdaptivityProvider>
	);
}

export default App;
