import React from 'react';
import PropTypes from 'prop-types';
import Particles from 'react-particles-js';
import bridge from '@vkontakte/vk-bridge';
import './top.css';

import {API, GETTOP, GETFRIENDS} from './api';

import { Panel, PanelHeader,Search,PanelHeaderBack,Header,Alert,HorizontalCell,ActionSheet,HorizontalScroll,ActionSheetItem,PanelSpinner,MiniInfoCell,Subhead, Tabs, TabsItem, Banner, Placeholder, List, Counter, SubnavigationBar, RichCell, SubnavigationButton, Title, PanelHeaderContent, Button, Group, Cell, Div, Avatar } from '@vkontakte/vkui';
import { Icon24CubeBoxOutline,Icon24ReorderIos,Icon24TextOutline,Icon24LogoVk,Icon20SkullOutline,Icon24PaymentCardOutline,Icon20ArticleOutline,Icon24HealthOutline,Icon20SmileOutline,Icon36Play,Icon24SkullOutline } from '@vkontakte/icons';

const TopUsers = ({ id, go, Info, Friends, setFriends, Referals, setReferals, fetchedUser,setPopout, TopContent, setTopContent, TopPlayers, setTopPlayers }) => (
	<Panel id={id} style={{background: "white"}}>
	<PanelHeader>Топ игроков</PanelHeader>
	<Group>
	<div style={{display: 'flex'}}>
    <Search id="search" placeholder="Введите id или имя игрока" onChange={() => {
    const searchstring = document.getElementById('search').value
		if(searchstring.length < 1 || searchstring == "" || searchstring == " "){
			setTopContent(['check'])
		}
		}}/>
	<Button stretched size="m" mode="overlay_primary" style={{color: '#ad8ef5'}} onClick={() => {
		const searchstring = document.getElementById('search').value
		if(searchstring.length < 1 || searchstring.length > 20){

		}else{
			async function search() {
				const array = await API('getusersbyid', 'text=' + encodeURIComponent(searchstring))
				setTopContent(array.response)
			}search()
		}
	}}>Поиск</Button>
	</div>
	  {TopContent.length === 0 && <center><Subhead weight="medium" style={{color: '#7f8285'}}>Ничего не найдено.</Subhead></center>}
		{TopContent[0] != 'check' && TopContent.length > 0 &&
              <React.Fragment>
                {TopContent.map((item) => {
                  return (
                    <Cell key={item.id} before={<Avatar src={item.infovk.photo_200} />} >{item.infovk.first_name} {item.infovk.last_name}</Cell>
                  )
              })}
        </React.Fragment>
      }
	</Group>
	<Group header={<Header mode="secondary"> Друзья в игре </Header>}>
	<HorizontalScroll showArrows getScrollToLeft={i => i - 120} getScrollToRight={i => i + 120}>
  <div style={{ display: 'flex' }}>
	<React.Fragment>
	{
		Friends[0] === 'check' && <PanelSpinner />
	}
	{
		Friends[0] === 'token' && <Div><center><Subhead weight="medium" style={{color: '#7f8285'}}>Вы не дали доступ к друзьям :(</Subhead></center></Div>
	}
	{
		Friends[0] != 'check' && Friends[0] != 'token' && Friends.length === 0 && <Div><center><Subhead weight="medium" style={{color: '#7f8285'}}>Нету друзей в игре, пригласи их :(</Subhead></center></Div>
	}
	{
		Friends[0] != 'check' && Friends[0] != 'token' && Friends.length > 0 &&
		<React.Fragment>
			{Friends.map((item) => {
				var i = 1;
				return (
					<HorizontalCell key={item.id} header={item.infovk.first_name}
					onClick={() => {
						setPopout(
						 <ActionSheet onClose={() => setPopout(null)}>
						 <Div>
						 <div className="parenttop">
							 <div className="div1top"> <Avatar size={120} src={item.infovk.photo_200}/> </div>
								 <div className="div2top">
									 <Title style={{marginLeft: '16px'}} level="1" weight="regular">{item.infovk.first_name} {item.infovk.last_name}</Title>
									 <MiniInfoCell before={<Icon24HealthOutline />}>
										 {item.owner === 0 ? "Не заражен(a)" : "Заражен(a)"}
									 </MiniInfoCell>
									 <MiniInfoCell before={<Icon24PaymentCardOutline />}>
										 {item.balance} ДНК
									 </MiniInfoCell>
									 <MiniInfoCell before={<Icon24TextOutline />}>
										 {item.virus.name}
									 </MiniInfoCell>
								 </div>
							 <div className="div3top" >
								<div style={{display: 'flex'}}>
									{item.owner == 0 ? <Button stretched className="buttons" before={<Icon24SkullOutline/>} >Заразить</Button> : <Button stretched before={<Icon24HealthOutline/>} className="buttons" >Вылечить</Button>}
									<Button stretched className="buttons" target="_blanc" href={`https://vk.com/id${item.id}`}  before={<Icon24LogoVk/>} style={{marginRight: 0}}> Профиль </Button>
								</div>
								<Button stretched style={{marginTop: 8}} onClick={() => setPopout(null)} className="buttons"> Закрыть </Button>
							</div>
						 </div>
						 </Div>
						 </ActionSheet>
						)
					}}
					>
                      <Avatar size={56} src={item.infovk.photo_200} />
          </HorizontalCell>
				)
				i++;
		})}
</React.Fragment>
	}
	</React.Fragment>
	</div>
  </HorizontalScroll>
	</Group>
	<Group header={<Header mode="secondary"> Топ 50 игроков </Header>}>
	<React.Fragment>
	{
		TopPlayers.length === 0 ? console.log(GETTOP(setTopPlayers)) : () => GETTOP(setTopPlayers)()
	}
	</React.Fragment>
	{TopPlayers.length === 0 && <PanelSpinner />}
	{TopPlayers.length > 0 &&
						<React.Fragment>
							{TopPlayers.map((item) => {
								var i = 1;
								return (
									<RichCell
									  key={item.id}
                    before={<React.Fragment><Avatar size={56} src={item.infovk.photo_200} /><div className="counter"><a>{item.mesto}</a></div></React.Fragment>}
                    text={`${item.balance} ДНК`}
                    after={<Icon20ArticleOutline fill="#ad8ef5"/>}
										onClick={() => {
											setPopout(
											 <ActionSheet onClose={() => setPopout(null)}>
											 <Div>
											 <div className="parenttop">
											   <div className="div1top"> <Avatar size={120} src={item.infovk.photo_200}/> </div>
											     <div className="div2top">
													   <Title style={{marginLeft: '16px'}} level="1" weight="regular">{item.infovk.first_name} {item.infovk.last_name}</Title>
														 <MiniInfoCell before={<Icon24HealthOutline />}>
	                             {item.owner == 0 ? "Не заражен(a)" : "Заражен(a)"}
	                           </MiniInfoCell>
														 <MiniInfoCell before={<Icon24PaymentCardOutline />}>
	                             {item.balance} ДНК
	                           </MiniInfoCell>
														 <MiniInfoCell before={<Icon24TextOutline />}>
	                             {item.virus.name}
	                           </MiniInfoCell>
													 </div>
											   <div className="div3top" >
												  <div style={{display: 'flex'}}>
                            {item.owner == 0 ? <Button stretched className="buttons" before={<Icon24SkullOutline/>} onClick={async () => {
															const response = await API('infect', 'toid=' + item.id)
															if(response.status === "good"){
																Referals.push(item)
															}
															setPopout(<Alert
															 actions={[{
															 title: 'Закрыть',
															 autoclose: true,
															 mode: 'cancel'
														 }]}
														 actionsLayout="vertical"
														 onClose={() => setPopout(null)}
														 header={response.status === 'good' ? "Успешно!" : "Неудачно!"}
														 text={response.message}
													 />)
														}}>Заразить</Button> : <Button stretched before={<Icon24HealthOutline/>} className="buttons" >Вылечить</Button>}
														<Button stretched className="buttons" target="_blanc" href={`https://vk.com/id${item.id}`}  before={<Icon24LogoVk/>} style={{marginRight: 0}}> Профиль </Button>
												  </div>
													<Button stretched style={{marginTop: 8}} onClick={() => setPopout(null)} className="buttons"> Закрыть </Button>
												</div>
											 </div>
											 </Div>
											 </ActionSheet>
											)
										}}
                    >{item.infovk.first_name} {item.infovk.last_name}
                  </RichCell>
								)
								i++;
						})}
			</React.Fragment>
		}
	</Group>
	<div style={{marginTop: '100px'}}>
	</div>
	</Panel>
);

TopUsers.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
	fetchedUser: PropTypes.shape({
		photo_200: PropTypes.string,
		first_name: PropTypes.string,
		last_name: PropTypes.string,
		city: PropTypes.shape({
			title: PropTypes.string,
		}),
	}),
};

export default TopUsers;
